@media screen and (min-width: 661px) {
  .jogs-list-container {
    margin-top: 176px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .list-item-container {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 23px;
  }

  .jog-icon {
    margin-top: 3px;
    margin-right: 46px;
    width: 88px;
    height: 88px;
    background-image: url("../images/icon.png");
    background-image: -webkit-image-set(
      url("../images/icon@2x.png") 2x,
      url("../images/icon@3x.png") 3x
    );
    background-image: image-set(
      url("../images/icon@2x.png") 2x,
      url("../images/icon@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .list-item-text-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .list-item-text-date {
    width: 73px;
    height: 16px;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #eaeaea;
    margin-bottom: 9px;
  }

  .list-item-text-jog-values-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 9px;
  }

  .list-item-text-jog-values-name {
    height: 16px;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: black;
  }

  .list-item-text-jog-values-value {
    margin-left: 4px;
    height: 16px;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #979797;
  }

  .add-button-container {
    min-height: calc(100vh) 0;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-top: 75px;
    margin-right: 40px;
    margin-bottom: 40px;
  }

  .add-button {
    width: 60px;
    height: 60px;
    background-image: url("../images/add.png");
    background-image: -webkit-image-set(
      url("../images/add@2x.png") 2x,
      url("../images/add@3x.png") 3x
    );
    background-image: image-set(
      url("../images/add@2x.png") 2x,
      url("../images/add@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }
}

@media screen and (max-width: 660px) {
  .jogs-list-container {
    margin-top: 77pt;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
  }

  .list-item-container {
    margin-top: 45pt;
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 132.4pt;
    width: 100%;
    border-bottom: solid 2pt rgb(215, 215, 215);
  }

  .jog-icon {
    margin-top: 3pt;
    margin-right: 26pt;
    width: 87pt;
    height: 87pt;
    background-image: url("../images/icon.png");
    background-image: -webkit-image-set(
      url("../images/icon@2x.png") 2x,
      url("../images/icon@3x.png") 3x
    );
    background-image: image-set(
      url("../images/icon@2x.png") 2x,
      url("../images/icon@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .list-item-text-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .list-item-text-date {
    width: 73pt;
    height: 16pt;
    font-size: 14pt;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #eaeaea;
    margin-bottom: 9pt;
  }

  .list-item-text-jog-values-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 9pt;
  }

  .list-item-text-jog-values-name {
    height: 16pt;
    font-size: 14pt;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
