@media screen and (min-width: 661px) {
  .let-me-in-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 323px;
    width: 503px;
    height: 379px;
    border-radius: 44px;
    background-color: #e990f9;
  }

  .bear-face {
    margin: 77px 171px 0px 172px;
    width: 156px;
    height: 150px;
    background-image: url("../images/bear-face.png");
    background-image: -webkit-image-set(
      url("../images/bear-face@2x.png") 2x,
      url("../images/bear-face@3x.png") 3x
    );
    background-image: image-set(
      url("../images/bear-face@2x.png") 2x,
      url("../images/bear-face@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .let-me-in {
    margin: 56px 176px 36px 176px;
    width: 151px;
    height: 60px;
    border-radius: 36px;
    border: solid 3px #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .let-me-in-text {
    width: 83px;
    height: 21px;
    font-size: 18px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffffff;
  }
}

@media screen and (max-width: 660px) {
