      url("../images/bearFace@2x.png") 2x,
      url("../images/bearFace@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .let-me-in {
    margin: 103pt 112pt 136pt 112pt;
    width: 151pt;
    height: 60pt;
    border-radius: 36pt;
    border: solid 3pt #e990f9;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .let-me-in-text {
    width: 83pt;
    height: 21pt;
    font-size: 18pt;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #e990f9;
  }
}
