@media screen and (min-width: 661px) {
  .jog-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 234px 468px 0px 469px;
    width: 503px;
    height: 379px;
    border-radius: 44px;
    background-color: #7ed321;
  }

  .cancel-container {
    margin: 45px 27px 0px 449px;
  }

  .cancel {
    width: 29px;
    height: 27px;
    background-image: url("../images/cancel.png");
    background-image: -webkit-image-set(
      url("../images/cancel@2x.png") 2x,
      url("../images/cancel@3x.png") 3x
    );
    background-image: image-set(
      url("../images/cancel@2x.png") 2x,
      url("../images/cancel@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .jog-form-input-container {
    margin: 50px 89px 0px 88px;
    display: flex;
    flex-direction: column;
  }

  .label-input-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  .label-text {
    width: 73.8px;
    height: 19px;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    margin-top: 5px;
    margin-bottom: 7px;
  }

  .input-text {
    width: 236px;
    height: 31px;
    border-radius: 7px;
    border: solid 1px #979797;
    background-color: #ffffff;
  }

  .save-button-container {
    margin: 21px 92px 47px 91px;
  }

  .save-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 320px;
    height: 42px;
    border-radius: 25.2px;
    border: solid 2px #ffffff;
    background-color: transparent;
  }

  .save-button-text {
    width: 71.1px;
    height: 15px;
    font-size: 12.6px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }
}

@media screen and (max-width: 660px) {
  .jog-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 159pt;
    width: 82%;
    height: 380pt;
    border-radius: 29pt;
    background-color: #7ed321;
  }

  .cancel-container {
    margin: 45px 21pt 0px 87%;
  }

  .cancel {
    width: 20pt;
    height: 19pt;
    background-image: url("../images/cancel.png");
    background-image: -webkit-image-set(
      url("../images/cancel@2x.png") 2x,
      url("../images/cancel@3x.png") 3x
    );
    background-image: image-set(
      url("../images/cancel@2x.png") 2x,
      url("../images/cancel@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .jog-form-input-container {
    margin-top: 26pt;
    display: flex;
    flex-direction: column;
    width: 77%;
  }

  .label-input-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 20pt;
  }

  .label-text {
    width: 53.8pt;
    height: 15pt;
    font-size: 13pt;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    margin-bottom: 6pt;
  }

  .input-text {
    height: 31pt;
    border-radius: 7pt;
    border: solid 1pt #979797;
    background-color: #ffffff;
  }

  .save-button-container {
    margin-top: 21pt;
    margin-bottom: 37pt;
    width: 77%;
  }

  .save-button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 42pt;
    border-radius: 25.2pt;
    border: solid 2pt #ffffff;
    background-color: transparent;
  }

  .save-button-text {
    width: 53pt;
    height: 15pt;
    font-size: 12.6pt;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }
}
