@media screen and (min-width: 661px) {
  .jog-info-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 201px;
  }

  .jog-info-header {
    width: 89px;
    height: 43px;
    font-size: 36px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #7ed321;
  }

  .jog-info-text-container {
    margin-top: 15px;
    margin-left: 4px;
  }

  .jog-info-text {
    width: 616px;
    height: 191px;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.71;
    letter-spacing: normal;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 660px) {
  .jog-info-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 101pt;
    margin-left: 25pt;
  }

  .jog-info-header {
    width: 62pt;
    height: 30pt;
    font-size: 25pt;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #7ed321;
    text-align: start;
  }

  .jog-info-text-container {
    margin-top: 6pt;
  }

  .jog-info-text {
    width: 90%;
    font-size: 12pt;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 24pt;
    letter-spacing: normal;
    margin-bottom: 30pt;
  }
}
