@media screen and (min-width: 661px) {
  .header-container {
    width: 100%;
    height: 116px;
    background-color: #7ed321;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .logo-container {
    margin-top: 30px;
    margin-left: 37px;
    margin-bottom: 30px;
  }

  .logo {
    width: 147px;
    height: 56px;
    background-image: url("../images/logo.png");
    background-image: -webkit-image-set(
      url("../images/logo@2x.png") 2x,
      url("../images/logo@3x.png") 3x
    );
    background-image: image-set(
      url("../images/logo@2x.png") 2x,
      url("../images/logo@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .navigation-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 381px;
    margin-top: 39px;
    margin-bottom: 38px;
  }

  .navigation-text {
    margin-top: 11px;
    height: 16px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    text-decoration: none;
  }

  .navigation-text:hover {
    height: 20.1px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    border-bottom: solid 2px #ffffff;
    text-decoration: none;
  }

  .filter {
    width: 39px;
    height: 39px;
    background-image: url("../images/filter-active.png");
    background-image: -webkit-image-set(
      url("../images/filter-active@2x.png") 2x,
      url("../images/filter-active@3x.png") 3x
    );
    background-image: image-set(
      url("../images/filter-active@2x.png") 2x,
      url("../images/filter-active@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 30px;
  }
}

@media screen and (max-width: 660px) {
  .header-container {
    width: 100%;
    height: 77pt;
    background-color: #7ed321;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .logo-container {
    margin: 20pt 0pt 20pt 25pt;
  }

  .logo {
    width: 96pt;
    height: 37pt;
    background-image: url("../images/logo.png");
    background-image: -webkit-image-set(
      url("../images/logo@2x.png") 2x,
      url("../images/logo@3x.png") 3x
    );
    background-image: image-set(
      url("../images/logo@2x.png") 2x,
      url("../images/logo@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .navigation-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 35%;
  }

  .filter {
    width: 39px;
    height: 39px;
    background-image: url("../images/filterActive.png");
    background-image: -webkit-image-set(
      url("../images/filterActive@2x.png") 2x,
      url("../images/filterActive@3x.png") 3x
    );
    background-image: image-set(
      url("../images/filterActive@2x.png") 2x,
      url("../images/filterActive@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 15%;
  }

  .menu {
    width: 30.5pt;
    height: 25pt;
    background-image: url("../images/menu.png");
    background-image: -webkit-image-set(
      url("../images/menu@2x.png") 2x,
      url("../images/menu@3x.png") 3x
    );
    background-image: image-set(
      url("../images/menu@2x.png") 2x,
      url("../images/menu@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 15%;
  }
}
