@media screen and (min-width: 661px) {
  .first-jog-container {
    margin-top: 356px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .sad-emoticon {
    width: 154px;
    height: 153.4px;
    background-image: url("../images/sad-rounded-square-emoticon.png");
    background-image: -webkit-image-set(
      url("../images/sad-rounded-square-emoticon@2x.png") 2x,
      url("../images/sad-rounded-square-emoticon@3x.png") 3x
    );
    background-image: image-set(
      url("../images/sad-rounded-square-emoticon@2x.png") 2x,
      url("../images/sad-rounded-square-emoticon@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .first-jog-text {
    margin-top: 47px;
    width: 182px;
    height: 58px;
    font-size: 24px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    text-align: center;
    color: #b0b0b0;
  }

  .create-button-container {
    margin-top: 141px;
  }

  .create-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 249px;
    height: 60px;
    border-radius: 36px;
    border: solid 3px #e990f9;
    background-color: transparent;
  }

  .create-button-text {
    width: 182px;
    height: 21px;
    font-size: 18px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #e990f9;
  }
}

@media screen and (max-width: 660px) {
  .first-jog-container {
    margin-top: 217pt;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .sad-emoticon {
    width: 87pt;
    height: 87pt;
    background-image: url("../images/sadRoundedSquareEmoticon.png");
    background-image: -webkit-image-set(
      url("../images/sadRoundedSquareEmoticon@2x.png") 2x,
      url("../images/sadRoundedSquareEmoticon@3x.png") 3x
    );
    background-image: image-set(
      url("../images/sadRoundedSquareEmoticon@2x.png") 2x,
      url("../images/sadRoundedSquareEmoticon@3x.png") 3x
    );
    background-size: contain;
    background-repeat: no-repeat;
  }

  .first-jog-text {
    margin-top: 30pt;
    width: 182pt;
    height: 58pt;
    font-size: 24pt;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 29pt;
    letter-spacing: normal;
    text-align: center;
    color: #b0b0b0;
  }

  .create-button-container {
    margin-top: 145pt;
  }

  .create-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 33%;
    min-width: 200pt;
    height: 60pt;
    border-radius: 36pt;
    border: solid 3pt #e990f9;
    background-color: transparent;
  }

  .create-button-text {
    margin-left: 10pt;
    width: 182pt;
    height: 21pt;
    font-size: 18pt;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #e990f9;
  }
}
